<template>
    <v-container fluid fill-height style="padding:0;" id="font" grid-list-md>
        <Blog :items="items" :time="time">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Explain">解释</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <p class=".body-1">1. 字体的单位</p>
                                    <table>
                                        <tr>
                                            <td> </td>
                                            <th>字体单位</th>
                                            <th>说明</th>
                                        </tr>
                                        <tr>
                                            <th>1</th>
                                            <td>px</td>
                                            <td>一个绝对单位，它导致了在任何情况下，页面上的文本所计算出来的像素值都是一样的</td>
                                        </tr>
                                        <tr>
                                            <th>2</th>
                                            <td>em</td>
                                            <td>一个相对父元素字体大小的单位，1em等于我们设计的当前元素的父元素上设置的字体大小</td>
                                        </tr>
                                        <tr>
                                            <th>3</th>
                                            <td>rem</td>
                                            <td>一个相对html根元素字体大小的单位</td>
                                        </tr>
                                        <tr>
                                            <th>4</th>
                                            <td>vw</td>
                                            <td>视口宽度的1/100</td>
                                        </tr>
                                        <tr>
                                            <th>5</th>
                                            <td>vh</td>
                                            <td>视口高度的1/100</td>
                                        </tr>
                                    </table>
                                    <p class=".body-1">2. 字体可以使用<code>text-shadow</code>设置阴影</p>
                                    <p style="text-indent: 1em;">text-shadow: 1px(x轴偏移量) 1px(y轴偏移量) 3px(模糊半径) black;</p>
                                    <p class=".body-1">3. 可以使用<code>letter-spacing</code>设置字母间距，<code>word-spacing</code>设置单词间距</p>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Result1">效果</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <p class="cssfont">层叠样式表(英文全称：<span class="decoration">Cascading Style Sheets</span>)是一种用来表现<span class="shadow">HTML</span>（标准通用标记语言的一个应用）或<span class="shadow">XML</span>（标准通用标记语言的一个子集）等文件样式的计算机语言。<span class="shadow">CSS</span>不仅可以静态地修饰网页，还可以配合各种脚本语言动态地对网页各元素进行格式化。<span class="shadow">CSS</span>能够对网页中元素位置的排版进行像素级精确控制，支持几乎所有的字体字号样式，拥有对网页对象和模型样式编辑的能力。</p>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Code1">代码</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                    <v-tab key="html" ripple>HTML</v-tab>
                                    <v-tab key="css" ripple>CSS</v-tab>
                                    <v-tab-item key="html">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">
                                                    {{html}}
                                                </pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                    <v-tab-item key="css">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">{{css}}</pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                </v-tabs>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-9-13",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            <p class="cssfont">层叠样式表(英文全称：
                <span class="decoration">Cascading Style Sheets</span>)是一种用来表现
                <span class="shadow">HTML</span>（标准通用标记语言的一个应用）或
                <span class="shadow">XML</span>（标准通用标记语言的一个子集）等文件样式的计算机语言。
                <span class="shadow">CSS</span>不仅可以静态地修饰网页，还可以配合各种脚本语言动态地对网页各元素进行格式化。
                <span class="shadow">CSS</span>能够对网页中元素位置的排版进行像素级精确控制，支持几乎所有的字体字号样式，拥有对网页对象和模型样式编辑的能力。
            </p>
            `,
            css: `
            .cssfont{
                font-family: cursive;
                letter-spacing: 2px;
                word-spacing: 4px;
            }
            .cssfont::first-letter{
                font-size: 2em;
            }
            .cssfont::selection {
                background-color: burlywood;
                color: white;
            }
            .cssfont .decoration{
                text-decoration: underline red wavy;
            }
            .cssfont .shadow{
                text-shadow: 1px 1px 3px black;
            }
            `
        };
    },

    mounted() {}
};
</script>

<style lang="scss">
#font {
    .blogcontent {
        padding-top: 20px;
    }
    .code {
        font-size: 12px;
        background-color: #f9f9f5;
        white-space: pre-wrap;
        word-wrap: break-word;
    }
    table {
        border-collapse: collapse;
        border: 2px solid rgb(100, 100, 100);
        letter-spacing: 1px;
        font-family: sans-serif;
        font-size: 0.7rem;
    }

    td,
    th {
        border: 1px solid rgb(100, 100, 100);
        padding: 10px 10px;
    }

    td {
        text-align: center;
    }
    .cssfont{
        font-family: cursive;
        letter-spacing: 2px;
        word-spacing: 4px;
    }
    .cssfont::first-letter{
        font-size: 2em;
    }
    .cssfont::selection {
        background-color: burlywood;
        color: white;
    }
    .cssfont .decoration{
        text-decoration: underline red wavy;
    }
    .cssfont .shadow{
        text-shadow: 1px 1px 3px black;
    }
}
</style>

